import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  AfterViewInit,
  SimpleChanges,
} from '@angular/core';
import { BcPaginatorBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

/**
 * Componente paginador
 *
 * @export
 * @class BcPaginatorComponent
 * @implements {OnInit}
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'bc-paginator',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: './bc-paginator.component.html',
})
export class BcPaginatorComponent implements OnInit, AfterViewInit {

  /**
   * Tipo de paginador.
   * @type {string}
   * @memberof BcPaginatorComponent
   */
  @Input() type = '';

  /**
   * Id del elemento.
   *
   * @type {string}
   * @memberOf BcPaginatorComponent
   */
  @Input() componentId: string = this.bcHelperService.getId(this);

  /**
   * Total de registros.
   *
   * @type {string}
   * @memberOf BcPaginatorComponent
   */
  @Input() totalRegisters: string;

  /**
   * Cantidad de registros a mostrar por página.
   *
   * @type {string}
   * @memberOf BcPaginatorComponent
   */
  @Input() perPage: string;

  /**
   * Múltiplo que se va usar para mostrar el dropdown perPage.
   * @type {string}
   * @memberOf BcPaginatorComponent
   */
  @Input() perMultiple: string;

  /**
   * Emitir número de página seleccionada.
   * @type {EventEmitter<number>}
   * @memberof BcPaginatorComponent
   */
  @Output()
  onChangePage: EventEmitter<number> = new EventEmitter<number>(true);
  /**
   * variable que hace referencia al comportamiento nativo del componente
   * @memberof BcPaginatorComponent
   */
  bcPaginatorBehavior: any;
   /**
   * Indica si es el primer cambio del componente
   * @type {boolean}
   * @memberof BcPaginatorComponent
   */
  firstChange: boolean = false;

  constructor(
    private bcHelperService: BcHelperService
  ) {}

  ngOnInit(): void { }
  /**
   * Indica cambios generados en el componente
   * @memberof BcPaginatorComponent
   * @param {SimpleChanges} changes
   */
  ngOnChanges(changes: SimpleChanges): void {
    if (this.firstChange) {
      this.bcPaginatorBehavior.getMySelf().innerHTML = ''
      setTimeout(() => {
        this.initPaginatorBehavior();
      })
    }
  }
  /**
   * Configuración del componente despues de cargar la vista
   * @memberof BcPaginatorComponent
   */
  ngAfterViewInit(): void {
    this.initPaginatorBehavior();
  }
  /**
   * Inicialización del paginador
   * @memberof BcPaginatorComponent
   */
  initPaginatorBehavior(): void {
    this.bcPaginatorBehavior = new BcPaginatorBehavior();
    this.bcPaginatorBehavior.setId(this.componentId);
    this.bcPaginatorBehavior.changeState = (param) => this.onChangePage.emit(param);
    this.bcPaginatorBehavior.setUp();
    this.firstChange = true;
  }

}